'use client';
import React, { useEffect, useRef, useState } from 'react';

const GOOGLE_MAPS_API_KEY = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '';

// 1. Define Props to communicate with the Home page
interface MapProps {
  isPinMode: boolean;
  setPinMode: (value: boolean) => void;
}

export default function GoogleMapsComponent({ isPinMode, setPinMode }: MapProps) {
  const mapRef = useRef<HTMLDivElement>(null);
  const mapInstanceRef = useRef<google.maps.Map | null>(null);
  const [isLoaded, setIsLoaded] = useState(false);

  // Load Google Maps Script
  useEffect(() => {
    if (window.google?.maps) {
      setIsLoaded(true);
      return;
    }
    const script = document.createElement('script');
    // Ensure libraries=marker is included for Advanced Markers
    script.src = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAPS_API_KEY}&v=weekly&libraries=marker`;
    script.async = true;
    script.onload = () => setIsLoaded(true);
    document.head.appendChild(script);
  }, []);

  // Initialize Map
  useEffect(() => {
    if (!isLoaded || !mapRef.current || mapInstanceRef.current) return;

    const map = new google.maps.Map(mapRef.current, {
      zoom: 15,
      center: { lat: 40.7200, lng: -73.8440 },
      mapId: '4504f8b37365c3d0', // REQUIRED for Advanced Markers. Use 'DEMO_MAP_ID' for testing.
      mapTypeControl: true,
      streetViewControl: true,
      fullscreenControl: true,
    });

    mapInstanceRef.current = map;
  }, [isLoaded]);

  // 2. LISTEN FOR CLICKS (The "Pinning" Logic)
  useEffect(() => {
    if (!mapInstanceRef.current || !isPinMode) return;

    const map = mapInstanceRef.current;

    // Change cursor to crosshair to indicate pinning mode
    map.setOptions({ draggableCursor: 'crosshair' });

    const clickListener = map.addListener('click', async (e: google.maps.MapMouseEvent) => {
      if (!e.latLng) return;

      // Import AdvancedMarkerElement library
      const { AdvancedMarkerElement } = await google.maps.importLibrary("marker") as google.maps.MarkerLibrary;

      // Create Redfin-style Pin (HTML Element)
      const pinContainer = document.createElement('div');
      pinContainer.className = 'bg-white text-blue-600 px-3 py-1 rounded-full border-2 border-blue-600 font-bold shadow-lg text-sm';
      pinContainer.innerText = '$ New';

      // Drop the pin
      new AdvancedMarkerElement({
        map,
        position: e.latLng,
        content: pinContainer,
      });

      // Reset UI: Turn off pin mode and reset cursor
      map.setOptions({ draggableCursor: null });
      setPinMode(false);
    });

    // Cleanup listener if user cancels or component unmounts
    return () => {
      google.maps.event.removeListener(clickListener);
      map.setOptions({ draggableCursor: null });
    };
  }, [isPinMode, setPinMode]);

  return (
    <div className="w-full h-full relative">
      {!isLoaded && (
        <div className="absolute inset-0 flex items-center justify-center bg-gray-100 z-20">
          Loading Map...
        </div>
      )}
      <div ref={mapRef} className="w-full h-full" />
    </div>
  );
}