'use client';

import React, { useState } from 'react';
import GoogleMapsComponent from './map';
import Topbar from './topbar';
import Search from './search';
import PinButton from './pinButton';

export default function Home() {
  const [isPinMode, setIsPinMode] = useState(false);

  return (
    // h-screen prevents the whole page from scrolling
    <main className="flex flex-col h-screen w-full overflow-hidden">
      
      {/* 1. Topbar stays at the very top */}
      <Topbar />

      {/* 2. Content Area (Flex Row for side-by-side) */}
      <div className="flex flex-1 overflow-hidden">
        
        {/* LEFT SIDE: Sidebar (Property List / Search) */}
        <aside className="w-[400px] h-full flex flex-col border-r bg-white shadow-lg z-10">
          <div className="p-4 border-b space-y-4">
            <PinButton 
              isPinning={isPinMode} 
              onClick={() => setIsPinMode(!isPinMode)} 
            />
          </div>
          
          {/* Scrollable list of properties */}
          <div className="flex-1 overflow-y-auto p-4">
            <h2 className="font-bold text-lg mb-4">Properties in this area</h2>
            {/* Property cards would go here */}
            <div className="p-4 bg-gray-50 rounded-lg border mb-3">Property 1</div>
            <div className="p-4 bg-gray-50 rounded-lg border mb-3">Property 2</div>
          </div>
        </aside>

        {/* RIGHT SIDE: The Map */}
        <section className="flex-1 h-full relative">
          <GoogleMapsComponent isPinMode={isPinMode} setPinMode={setIsPinMode} />
        </section>

      </div>
    </main>
  );
}